import '@/plugins/axios'


export function queryVip(){
  //  const url = 'http://192.168.43.102:8080/vip/queryVip';
  const url = 'http://localhost:8080/vip/queryVip';
  return axios.post(url)
}
export function queryVipById(data){
  // const url = 'http://192.168.43.102:8080/vip/queryVipById';
  const url = 'http://localhost:8080/vip/queryVipById';
 let vipData={"phoneNum":data}
 return axios.post(url,vipData)
}
export function addVip(data){
  // const url='http://192.168.43.102:8080/vip/addVip'
  const url='http://localhost:8080/vip/addVip'
  return axios.post(url,data)
}

export function queryDiscount(){
  // const url='http://192.168.43.102:8080/vip/queryDiscount'
  const url='http://localhost:8080/vip/queryDiscount'
  return axios.post(url)
}

export function setDiscount(data){
  // const url='http://192.168.43.102:8080/vip/setDiscount'
  const url='http://localhost:8080/vip/setDiscount'
  let cut={"discount":data}
  return axios.post(url,cut)
}


