import '@/plugins/axios'

//查询所有商品类别 
/**goodsCategory:[
 {
      goodsCategoryId:1,
      category:"小吃"
  }
  ] */
export function queryGoodsCategory(){
// const url = 'http://192.168.43.102:8080/goodsCategory/queryGoodsCategory'
const url = 'http://localhost:8080/goodsCategory/queryGoodsCategory'
  return axios.get(url)
}

//查询一个类别的商品 goodsCategoryId

export function queryGoodsById(data){
//  const url = 'http://192.168.43.102:8080/goods/queryGoodsByCategory'
const url = 'http://localhost:8080/goods/queryGoodsByCategory'
 
  let goods={"typeId":data}
  return axios.post(url,goods)
}

//添加商品
/**good:[
 {
    name:"小吃",
    price:”123.213”,
    goodsCategoryId:”1”,
pictureUrl:”adafadfaf.jpg”
    }
  ]
 */
export function addGood(data){
  // const url = 'http://192.168.43.102:8080/goods/addGood'
  const url = 'http://localhost:8080/goods/addGood'
  let good={"good":data}
  return axios.post(url,good)
}

//删除商品 goodsId
export function deleteGoodsCategory(data){
  // const url = 'http://192.168.43.102:8080/goods/deleteGoodsCategory'
  const url = 'http://localhost:8080/goods/deleteGoodsCategory'
  let good={"goodsId":data}
  return axios.post(url,good)
}

//添加种类 category

export function addGoodsCategory(data){
  // const url = 'http://192.168.43.102:8080/goodsCategory/addGoodsCategory'
  const url = 'http://localhost:8080/goodsCategory/addGoodsCategory'
  let goods={"category":data.name}
  return axios.post(url,goods)
}

